<template>

  <div id="countdown">

    <!-- <div id="percentage">
      {{percentage}}
    </div> -->

    <div id="percentage">
      <circular-loading-bar :percentage="percentage"/>
    </div>

    <div id="title">
      {{ this.cur }}
      {{ this.title }}
    </div>

  </div>

</template>

<script>

import circularLoadingBar from "@/components/circularLoadingBar.vue";

export default {
  props: ["cur", "until", "title", "mode"],
  name: "countdown",
  components: {circularLoadingBar},
  data() {
    return {

    }
  },
  computed: {
    percentage() {

      let current;
      if (this.cur === null) {
        current = 0;
      } else {
        current = this.cur;
      }

      return this.truncate(current / this.until);
    },
  },

  methods: {
    truncate(number) {
      return number.toString().match(/^-?\d+(?:\.\d{0,3})?/)[0];
    }
  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#countdown
  width: 10rem
  display: flex
  flex-direction: column

  align-items: center

  border: 2px solid $base-button-border
  border-radius: 10px

  box-shadow: $base-shadow
  margin: 1rem

  backdrop-filter: blur(1rem)

  font-size: 1.4rem

#countdown:hover
  cursor: pointer

#countdown:active
  box-shadow: none

#percentage
  padding-top: 2.5rem
  padding-bottom: 1rem
  text-align: center

#title
  font: $base-font
  color: $base-font-color

  text-align: center

.progress-ring__circle
  transition: 0.35s stroke-dashoffset
  transform: rotate(-90deg)
  transform-origin: 50% 50%

</style>