    this.$websocket.on("channel_updated", (response) => {
      this.$store.commit("overwriteChannel", {chID: response.channel.channelID, newChannel: response.channel});
    });

    this.$websocket.on("user_deleted", (response) => {
      this.$store.commit("removeUser", response.id);
    });

    this.$websocket.on("user_updated", (response) => {
