<template>

  <div id="countdown">

    <div id="percentage">
      {{ tts }}
    </div>

    <div id="title">
      seconds
    </div>

  </div>

</template>

<script>

export default {
  props: ["msec"],
  name: "total",
  data() {
    return {

    }
  },
  computed: {
    tts() {
      let divString = (this.msec / 1000).toString();

      if (divString.includes('.')) {
        let rest = divString.split('.');
        let endPadded = rest[1].padEnd(3, '0');

        return rest[0] + '.' + endPadded;
      } else {
        return divString;
      }
    }
  },

  methods: {

  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#countdown
  width: 10rem
  display: flex
  flex-direction: column

  align-items: center

  border: 2px solid $base-button-border
  border-radius: 10px

  box-shadow: $base-shadow
  margin: 1rem

  backdrop-filter: blur(1rem)

  font-size: 1.4rem

#percentage
  padding-top: 2.5rem
  padding-bottom: 1rem
  text-align: center

#title
  font: $base-font
  color: $base-font-color

  text-align: center

</style>