<template>

  <div id="body">

    <div id="title">
      <title-tile :title="'meeting her ðŸ’œ'"/>
    </div>

    <div id="total">
      <total :msec="this.totalSeconds"/>
    </div>

    <div id="days">
      <countdown :cur="this.days" :until="356" :title="'days'" :mode="null"/>
    </div>

    <div id="hours">
      <countdown :cur="this.hours" :until="24" :title="'hours'" :mode="null"/>
    </div>

    <div id="minutes">
      <countdown :cur="this.minutes" :until="60" :title="'minutes'" :mode="null"/>
    </div>

    <div id="seconds">
      <countdown :cur="this.seconds" :until="60" :title="'seconds'" :mode="null"/>
    </div>

  </div>

</template>

<script>
import countdown from "@/components/countdown.vue";
import TitleTile from "@/components/titleTile.vue";
import Total from "@/components/totalSeconds.vue";

export default {
  name: "WelcomeScreen",
  components: {Total, TitleTile, countdown},
  data() {
    return {
      totalSeconds: 0
    }
  },
  computed: {
    days() {
      return Math.floor(this.totalSeconds / 86400000);
    },
    hours() {
      return this.diff.getHours() - 1;
    },
    minutes() {
      return this.diff.getMinutes();
    },
    seconds() {
      return this.diff.getSeconds();
    },
    diff() {
      return new Date(this.totalSeconds);
    }
  },
  mounted() {
    let mainLoop = setInterval(() =>{
      let now = Date.now();
      this.totalSeconds = this.$store.state.finalDate - now;
    }, 100);
  }
}
</script>

<style lang="sass" scoped>
@import "@/assets/variables.sass"


#body
  display: grid
  grid-template-columns: repeat(5, 1fr)
  grid-template-rows: repeat(4, 1fr)

  place-items: center

#total
  grid-column: 1
  grid-row: 2

#title
  grid-column: 1

#days
  grid-column: 1
  grid-row: 3

#hours
  grid-column: 2
  grid-row: 3

#minutes
  grid-column: 1
  grid-row: 4

#seconds
  grid-column: 2
  grid-row: 4

@media only screen and (max-width: 600px)
  #body
    display: grid
    grid-template-columns: auto
    grid-template-rows: repeat(6, 1fr)

    place-items: center

  #total
    grid-row: 2

  #days
    grid-row: 6

  #hours
    grid-column: 1
    grid-row: 5

  #minutes
    grid-row: 4

  #seconds
    grid-column: 1
    grid-row: 3

</style>