<template>

  <div id="progress">
    <div id="barOverflow">
      <div id="bar"></div>
    </div>
  </div>

</template>

<script>

export default {
  props: ["percentage"],
  name: "circularLoadingBar",
  data() {
    return {

    }
  },
  computed: {
    calculatedPercentage() {
      return `rotate(${45+(this.percentage*180)}deg)`;
    }
  },

  methods: {

  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#progress
  position: relative
  float: left
  text-align: center

#barOverflow
  position: relative
  overflow: hidden

  width: 100px
  height: 50px

  margin-bottom: -14px

#bar
  position: absolute
  top: 0
  left: 0
  width: 90px
  height: 90px

  border-radius: 50%
  box-sizing: border-box
  border: 5px solid white

  border-bottom-color: $base-button-border
  border-right-color: $base-button-border
  transform: v-bind('calculatedPercentage')

</style>